import React, { useEffect } from 'react';
import { useMaxBridgeContext } from '../providers/MaxBridgeProvider';
import styled from 'styled-components';

const Container = styled.div`
  padding: 20px;
  max-width: 600px;
  margin: 0 auto;
`;

const UserInfo = styled.div`
  background: #e8f5e8;
  border: 1px solid #c3e6cb;
  border-radius: 8px;
  padding: 16px;
  margin-bottom: 20px;

  h2 {
    margin: 0 0 8px 0;
    color: #155724;
  }

  p {
    margin: 4px 0;
    color: #0f5132;
  }
`;

const ActionsGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 12px;
  margin-top: 20px;
`;

const Button = styled.button`
  background: #007bff;
  color: white;
  border: none;
  border-radius: 6px;
  padding: 12px 16px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.2s;

  &:hover {
    background: #0056b3;
  }

  &:active {
    transform: translateY(1px);
  }
`;

const WarningMessage = styled.div`
  background: #fff3cd;
  border: 1px solid #ffeaa7;
  border-radius: 6px;
  padding: 16px;
  text-align: center;

  h1 {
    margin: 0 0 8px 0;
    color: #856404;
    font-size: 18px;
  }

  p {
    margin: 0;
    color: #856404;
  }
`;

export const MaxDemo: React.FC = () => {
  const { isMaxApp, user, showBackButton, onBackButtonClick, hapticFeedback, openExternalLink } =
    useMaxBridgeContext();

  useEffect(() => {
    if (isMaxApp) {
      showBackButton(true);
      onBackButtonClick(() => {
        hapticFeedback('impact', { style: 'light' });
        console.log('Back button pressed');
      });
    }

    return () => {
      if (isMaxApp) {
        showBackButton(false);
      }
    };
  }, [isMaxApp, showBackButton, onBackButtonClick, hapticFeedback]);

  if (!isMaxApp) {
    return (
      <Container>
        <WarningMessage>
          <h1>   MAX</h1>
          <p>     MAX </p>
        </WarningMessage>
      </Container>
    );
  }

  return (
    <Container>
      <h1> MAX Bridge</h1>

      {user && (
        <UserInfo>
          <h2>, {user.first_name}!</h2>
          <p>Username: @{user.username || ' '}</p>
          <p>: {user.language_code || ' '}</p>
        </UserInfo>
      )}

      <ActionsGrid>
        <Button onClick={() => hapticFeedback('impact', { style: 'light' })}>
           
        </Button>

        <Button onClick={() => hapticFeedback('impact', { style: 'medium' })}>
           
        </Button>

        <Button onClick={() => hapticFeedback('impact', { style: 'heavy' })}>
           
        </Button>

        <Button onClick={() => hapticFeedback('notification')}></Button>

        <Button onClick={() => openExternalLink('https://example.com')}> </Button>

        <Button
          onClick={() => {
            hapticFeedback('selection');
            console.log('Selection changed');
          }}
        >
           
        </Button>
      </ActionsGrid>
    </Container>
  );
};
